<template>
  <div>
    <h2 v-if='tasks.length === 0' class="pa-5 text-center display-1 font-weight-light"> Текущих задач нет. </h2> 
  <v-timeline v-else align-top dense>
      <v-scale-transition group>
      <v-timeline-item
        v-for="(item, index) in tasks"
        :key="item.taskTitle + item.datetime"
        :color=item.color
        fill-dot
        :icon="'mdi-48px '+ item.icon"
        class="pr-5"
      >
        <v-card :color=item.color>
          <v-btn icon class="float-right" @click="removeTask(index)">
            <v-icon color="white">mdi-close-circle-outline</v-icon>
          </v-btn>
          <v-card-title>
            <h2 :class='[$vuetify.breakpoint.smAndDown ? "headline" : "font-weight-light", "white--text"]'>{{item.status}}</h2>
          </v-card-title>
          <v-card-subtitle>
            <h2 class="font-weight-light mt-2 white--text">{{item.taskTitle}}</h2>
          </v-card-subtitle>

          <v-card-text class="hidden-sm-and-down pt-2 white text--primary">
            <p class="hidden-sm-and-down mb-2">Название курса: {{ item.courseTitle }}</p>
            <p class="hidden-sm-and-down mb-2">Название задания: {{ item.taskTitle }}</p>
            <div class="pt-2 d-flex justify-space-between align-center">
              <v-btn
                :color="item.color"
                class="mx-0"
                outlined
              >
                {{item.buttonTitle}}
              </v-btn>
              <div class="overflow">
                <v-icon class="float-left">mdi-48px mdi-calendar</v-icon>
                <div class=float-right>
                  <div class=time>{{getTime(item.dateTime)}}</div>
                  <div class=date>{{getDate(item.dateTime)}}</div>
                </div>
              </div>
            </div>
          </v-card-text>
          <!-- <v-container>
            <v-row>
              <v-col cols="12" md="10">
                Lorem ipsum dolor sit amet, no nam oblique veritus. Commune scaevola imperdiet nec ut, sed euismod convenire principes at. Est et nobis iisque percipit.
              </v-col>
              <v-col
                class="hidden-sm-and-down text-right"
                md="2"
              >
                <v-icon size="64">mdi-calendar-text</v-icon>
              </v-col>
            </v-row>
          </v-container> -->
        </v-card>
      </v-timeline-item>
    </v-scale-transition>
  </v-timeline>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  data: () => ({
    tasks: [
      {
        status: 'Просрочено',
        color: 'error',
        icon: 'mdi-alert-circle-outline',
        taskTitle: "Тест №1. Волновые характеристики электронов",
        courseTitle: "Квантовая физика",
        buttonTitle: 'Выполнить',
        dateTime: new Date(Date.now() - 1000 * 60 * 60),
      },
      {
        status: 'Скоро истекает',
        color: 'warning',
        icon: 'mdi-alert-circle-outline',
        taskTitle: "Тест №2",
        courseTitle: "Квантовая физика",
        buttonTitle: 'Выполнить',
        dateTime: new Date(Date.now() + 1000 * 60 * 21),
      },
      {
        status: 'Скоро истекает',
        color: 'warning',
        icon: 'mdi-alert-circle-outline',
        taskTitle: "Задание №1",
        courseTitle: "Математическая статистика",
        buttonTitle: 'Выполнить',
        dateTime: new Date(Date.now() + 5 * 1000 * 60 * 35),
      },
      {
        status: 'В очереди',
        color: 'info',
        icon: 'mdi-information-outline',
        taskTitle: "Тест №3",
        courseTitle: "Квантовая физика",
        buttonTitle: 'Выполнить',
        dateTime: new Date(Date.now() + 5 * 24 * 1000 * 60 * 60),
      },
    ]
  }),
  methods: {
    removeTask(index) {
      this.tasks = this.tasks.filter((x, i) => i !== index);
    },
    getDate(dateTime) {
      const options = {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric'
      };

      return dateTime.toLocaleString("ru", options);
    },
    getTime(dateTime) {
      const options = {
        hour: 'numeric',
        minute: 'numeric'
      };

      return dateTime.toLocaleString("ru", options);
    },
  } 
}
</script>

